import React, { useState, useEffect } from "react";
import './App.css';
import {Bar} from 'react-chartjs-2';

function App() {
  const [data, setData] = useState(undefined);

  const getFormattedData = (type, values) => {
    return {
      labels: ['Day-4', 'Day-3', 'Day-2', 'Yesterday', 'Today'],
      datasets: [
        {
          label: (type === 1? "Earned Coins": type === 2? "Spent Coins": type === 3? "Users": type === 4? "Campaigns": "Paid Transactions"),
          backgroundColor: 'rgba(75,192,192,1)',
          borderColor: 'rgba(0,0,0,1)',
          borderWidth: 1,
          data: values.split(",")
        }
      ]
    }
  }

  useEffect(() => {
    let key = window.location.search;
    if(key !== undefined && key.includes("=") && key.includes("&")){ 
      fetch('https://'+ key.split("&")[0].split("=")[1]+'.execute-api.ap-south-1.amazonaws.com/' + key.split("&")[1].split("=")[1])
      .then(response => response.json())
      .then(data => {
        setData(data)
      });
    }
  }, [setData]);

  return (
    <div className="App">
      { data ? (
        <div style={{flex:1}}>
        <div className='row' style={{display:"flex"}}>
          <p style={{flex:1}}>Active Campigns: {data.active_camps}</p>
            <p style={{flex:1}}>Pending Payout Amount: ${data.pending_payout_amount}</p>
            <p style={{flex:1}}>Pending Payouts: {data.pending_payouts}</p>
            <p style={{flex:1}}>Total Amount Paid: ${data.total_amount_paid}</p>
        </div>
          <div className='row' style={{display:"flex"}}>
          <div style={{flex:2}}>
            <Bar
              style={{padding: 50}}
              data={getFormattedData(1, data["earned_coins"])}
              options={{
                title:{
                  display:true,
                  text:'Average Rainfall per month',
                  fontSize:20
                },
                legend:{
                  display:true,
                  position:'right'
                }
              }}
            />
          </div>
          <div style={{flex:2}}>
            <Bar
              style={{padding: 50}}
              data={getFormattedData(2, data["spent_coins"])}
              options={{
                title:{
                  display:true,
                  text:'Average Rainfall per month',
                  fontSize:20
                },
                legend:{
                  display:true,
                  position:'right'
                }
              }}
            />
          </div>
        </div>
        <div className='row' style={{display:"flex"}}>
          <div style={{flex:1}}>
            <Bar
              style={{padding: 50}}
              data={getFormattedData(3, data["users"])}
              options={{
                title:{
                  display:true,
                  text:'Average Rainfall per month',
                  fontSize:20
                },
                legend:{
                  display:true,
                  position:'right'
                }
              }}
            />
          </div>
          <div style={{flex:1}}>
            <Bar
              style={{padding: 50}}
              data={getFormattedData(4, data["camps"])}
              options={{
                title:{
                  display:true,
                  text:'Average Rainfall per month',
                  fontSize:20
                },
                legend:{
                  display:true,
                  position:'right'
                }
              }}
            />
          </div>
        </div>
        <div className='row' style={{display:"flex"}}>
          <div style={{flex:1}}>
            <Bar
              style={{padding: 50}}
              data={getFormattedData(5, data["paid_transactions"])}
              options={{
                title:{
                  display:true,
                  text:'Average Rainfall per month',
                  fontSize:20
                },
                legend:{
                  display:true,
                  position:'right'
                }
              }}
            />
          </div>
          <div style={{flex:1}}>
          </div>
        </div>
      </div>
    ):(
      <p>Loading...</p>
    )}
    </div>
  );
}

export default App;
